import {Component, OnInit } from '@angular/core';
import {Author} from '../models/author';
import {DataGetterService} from '../services/data-getter.service';
import {ActivatedRoute, Router} from '@angular/router';
import {CollectionName} from '../models/collection-name.enum';
import {environment} from '../../environments/environment';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';

@Component({
  selector: 'app-edit-author',
  templateUrl: './edit-author.component.html',
  styleUrls: ['./edit-author.component.scss']
})
export class EditAuthorComponent implements OnInit {

  author: Author = new Author();
  authorImgURL: string;
  newAuthorImgFile;
  editor = ClassicEditor;
  shortInfoWarning = false;

  constructor(public readonly dataGetterService: DataGetterService,
              public readonly activatedRoute: ActivatedRoute,
              public readonly router: Router) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((author: Author) => {
      this.author = Author.clone(author);
      this.dataGetterService.GetImgURL(this.author.imgURI).subscribe(url => this.authorImgURL = url);
    });
  }

  ngOnChange(changes: string) {
    this.shortInfoWarning = changes.length > 15;
  }

  SetAuthorImg(data) {
    this.newAuthorImgFile = data.target.files[0];
    const path = `${CollectionName.sections}/${this.author.sectionId}/${CollectionName.authors}/${this.author.id}/authorImg`;
    this.dataGetterService.UploadFile(path, this.newAuthorImgFile)
      .subscribe(value => {
        value.ref.getDownloadURL().then(value1 => this.authorImgURL = value1);
        this.author.imgURI = `gs://${environment.firebaseConfig.storageBucket}/${path}`;
      });
  }

  DeleteAuthor() {
    this.dataGetterService.DeleteAuthor(this.author);
    alert(`Author deleted!`);
    this.dataGetterService.GetSection(this.author.sectionId).subscribe(section => this.router.navigate(['/section'], {queryParams: section} ));
  }

  SaveAuthor() {
    this.dataGetterService.SetAuthor(this.author)
      .subscribe(
        value => {
          alert(`Author saved!`);
          this.router.navigate(['/author'], {queryParams: this.author});
        },
        error => {
          alert(`Error ${error}`);
        });
  }
}
