import { Component, OnInit } from '@angular/core';
import {Author} from '../models/author';
import {StorageFile} from '../models/storage-file';
import {ActivatedRoute, Router} from '@angular/router';
import {DataGetterService} from '../services/data-getter.service';

@Component({
  selector: 'app-edit-library',
  templateUrl: './edit-library.component.html',
  styleUrls: ['./edit-library.component.scss']
})
export class EditLibraryComponent implements OnInit {

  author: Author;
  libraryFiles: StorageFile[];

  constructor(public readonly activatedRoute: ActivatedRoute,
              public readonly dataGetterService: DataGetterService,
              public readonly router: Router) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((author: any) => {
      this.author = author;
      this.dataGetterService.GetAuthorLibraryFiles(this.author).subscribe(res => this.libraryFiles = res);
    });
  }

  removeLibraryFile(index: number) {
    // this.social_networks.splice(index, 1);
    this.dataGetterService.DeleteAuthorLibraryFile(this.author, this.libraryFiles[index]);
  }

}
