import { Component, OnInit } from '@angular/core';
import {DataGetterService} from '../services/data-getter.service';
import {Footer} from '../models/footer';
import {FooterSocialnetwork} from '../models/footer-socialnetwork';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {

  footer = new Footer();
  socialNetworks: FooterSocialnetwork[];

  constructor(public readonly dataGetterService: DataGetterService) { }

  ngOnInit(): void {
    this.dataGetterService.GetFooter().subscribe(footer => this.footer = footer);
    this.dataGetterService.GetFooterSocialNetworks().subscribe(result => this.socialNetworks = result);
  }
}
