import { Component, OnInit } from '@angular/core';
import {DataGetterService} from '../services/data-getter.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  username: string;
  password: string;

  constructor(protected dataGetterService: DataGetterService,
              protected router: Router) { }

  ngOnInit(): void {
    if (this.dataGetterService.userAuthenticated()) {
      this.router.navigate(['/home']);
    }
  }

  tryLogin() {
    this.dataGetterService.checkUser(this.username, this.password).then(
      res => {
        // this.dataGetterService.setUser(this.username);
        // console.log({ login: this.username, password: this.password});
        this.router.navigate(['/home']);
      },
      err => {
        alert(err.message);
      }
    );
  }

}
