import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DataGetterService } from '../services/data-getter.service';
import {Author} from '../models/author';

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss']
})
export class VideoComponent implements OnInit {

  author: Author;
  videoURL:string = "../../assets/video/poem.mp4";
  videoURLs = [
    "../../assets/video/poem.mp4",
    "../../assets/video/gold.mp4",
    "../../assets/video/people.mp4"
  ];
  constructor(public readonly activatedRoute: ActivatedRoute,
    public readonly dataGetterService: DataGetterService,
    public readonly router: Router) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((author: any) => {
      this.author = author;
    });
  }

  backToAuthor() {
    this.router.navigate(['/author'], {queryParams: this.author});
  }

}
