import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {AppComponent} from './app.component';
import {HomeComponent} from './home/home.component';
import {NotFoundComponent} from './not-found/not-found.component';
import {SectionComponent} from './section/section.component';
import {LoginComponent} from './login/login.component';
import {EditFooterComponent} from './edit-footer/edit-footer.component';
import {EditGuard} from './guards/edit.guard';
import {AuthorComponent} from './author/author.component';
import { EditAuthorComponent} from './edit-author/edit-author.component';
import {BiographyComponent} from './biography/biography.component';
import {EditBiographyComponent} from './edit-biography/edit-biography.component';
import {LibraryComponent} from './library/library.component';

import {EditLibraryComponent} from './edit-library/edit-library.component';
import {IndicatorsComponent} from './indicators/indicators.component';
import {EditIndicatorsComponent} from './edit-indicators/edit-indicators.component';
import {PhotosComponent} from './photos/photos.component';
import { VideoComponent } from './video/video.component';

const routes: Routes = [
  { path: '', component: HomeComponent},
  { path: 'login', component: LoginComponent},
  { path: 'home', redirectTo: '/'},
  { path: 'section', component: SectionComponent},
  { path: 'author', component: AuthorComponent},
  { path: 'biography', component: BiographyComponent},
  { path: 'video', component: VideoComponent},
  { path: 'library', component: LibraryComponent},
  { path: 'indicators', component: IndicatorsComponent},
  { path: 'photos', component: PhotosComponent},
  { path: 'edit-footer', component: EditFooterComponent, canActivate: [EditGuard]},
  { path: 'edit-author', component: EditAuthorComponent, canActivate: [EditGuard]},
  { path: 'edit-biography', component: EditBiographyComponent, canActivate: [EditGuard]},
  { path: 'edit-library', component: EditLibraryComponent, canActivate: [EditGuard]},
  { path: 'edit-indicators', component: EditIndicatorsComponent, canActivate: [EditGuard]},
  { path: '**', component: NotFoundComponent},

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
