import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Section} from '../models/section';
import {Author} from '../models/author';
import {DataGetterService} from '../services/data-getter.service';

@Component({
  selector: 'app-author',
  templateUrl: './author.component.html',
  styleUrls: ['./author.component.scss']
})
export class AuthorComponent implements OnInit {

  author: Author;
  authorImgURL: string;

  constructor(public readonly activatedRoute: ActivatedRoute,
              public readonly dataGetterService: DataGetterService,
              public readonly router: Router
  ) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((author: any) => {
      this.author = author;
      this.dataGetterService.GetImgURL(this.author.imgURI).subscribe(url => this.authorImgURL = url);
    });
  }

  backToSection() {
    this.dataGetterService
      .GetSection(this.author.sectionId)
      .subscribe(section => { this.router.navigate(['/section'], {queryParams: section}); });
  }

}
