import { Component, OnInit } from '@angular/core';
import {Section} from '../models/section';
import {DataGetterService} from '../services/data-getter.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  imgUrls = [];
  curImageIndex = 0;
  curImage: string;
  sections: Section[];
  constructor(public dataGetterService: DataGetterService) {
  }

  ngOnInit(): void {
    this.dataGetterService.GetSections().subscribe(sections => {
      this.sections = sections;
      this.getAuthorImages();
      this.currentImage();
    });
  }

  getAuthorImages()
  {
      for (const section of this.sections)
      {
        this.dataGetterService.GetAuthors(section.id).subscribe(authors =>
        {
          for (const author of authors)
          {
            this.dataGetterService.GetImgURL(author.imgURI).subscribe(url => { this.imgUrls.push(url); console.log(this.imgUrls); });
          }
         });
      }
  }


  changeCurImage(forward: boolean){
    if (forward){
      this.curImageIndex++;
    } else {
      this.curImageIndex--;
    }

    if (this.curImageIndex >= this.imgUrls.length){
      this.curImageIndex = 0;
    }
    if (this.curImageIndex < 0){
      this.curImageIndex = this.imgUrls.length - 1;
    }
    this.curImage = this.imgUrls[this.curImageIndex];
  }

  currentImage()
  {
    this.curImage = this.imgUrls[this.curImageIndex];
    setInterval(() => {
      this.changeCurImage(true);
    }, 1000);
  }

}
