import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {StorageFile} from '../models/storage-file';
import {DataGetterService} from '../services/data-getter.service';

@Component({
  selector: 'app-photo-file',
  templateUrl: './photo-file.component.html',
  styleUrls: ['./photo-file.component.scss']
})
export class PhotoFileComponent implements OnInit {

  @Input() file: StorageFile;
  @Output() showPhotoEvent = new EventEmitter();
  photoURL: string;
  constructor(public readonly dataGetterService: DataGetterService) { }

  ngOnInit(): void {
    this.dataGetterService.GetImgURL(this.file.uri).subscribe(url => this.photoURL = url);
  }

  openFileInNewTab() {
    this.dataGetterService.GetFileURL(this.file.uri).subscribe(url => {     window.open(url, '_blank'); console.log(url); } );
  }

  showPhoto() {
    this.showPhotoEvent.emit(this.file);
  }

}
