import { Component, OnInit } from '@angular/core';
import {DataGetterService} from '../services/data-getter.service';
import {Footer} from '../models/footer';
import {Router} from '@angular/router';
import {FooterSocialnetwork} from '../models/footer-socialnetwork';

@Component({
  selector: 'app-edit-footer',
  templateUrl: './edit-footer.component.html',
  styleUrls: ['./edit-footer.component.scss']
})
export class EditFooterComponent implements OnInit {

  footer: Footer;
  social_networks: FooterSocialnetwork[];

  constructor(public readonly dataGetterService: DataGetterService,
              public readonly router: Router) { }

  ngOnInit(): void {
    this.dataGetterService.GetFooter().subscribe(footer => this.footer = footer);
    this.dataGetterService.GetFooterSocialNetworks().subscribe(result => this.social_networks = result);
  }

  addSocialNetworkToArray() {
    this.social_networks.push(new FooterSocialnetwork());
  }

  removeSocialNetworkFromArray(index: number) {
    // this.social_networks.splice(index, 1);
    this.dataGetterService.DeleteFooterSocialNetwork(this.social_networks[index]);
  }
  setFooter() {
    this.dataGetterService.SetFooter(this.footer).then(res => {
      // location.reload();
    });
    this.dataGetterService.SetFooterSocialNetworks(this.social_networks);
    //location.reload();
    //this.dataGetterService.GetFooter().subscribe(footer => this.footer = footer);
    //window.location.reload();
  }
}
