import { Component, OnInit } from '@angular/core';
import {Author} from '../models/author';
import {StorageFile} from '../models/storage-file';
import {ActivatedRoute, Router} from '@angular/router';
import {DataGetterService} from '../services/data-getter.service';
import {CollectionName} from '../models/collection-name.enum';
import {environment} from '../../environments/environment';

@Component({
  selector: 'app-photos',
  templateUrl: './photos.component.html',
  styleUrls: ['./photos.component.scss']
})
export class PhotosComponent implements OnInit {

  author: Author;
  photosFiles: StorageFile[];
  photoViewerEnabled = false;
  currentPhotoURL: string;

  constructor(public readonly activatedRoute: ActivatedRoute,
              public readonly dataGetterService: DataGetterService,
              public readonly router: Router) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((author: any) => {
      this.author = author;
      this.dataGetterService.GetAuthorPhotosFiles(this.author).subscribe(res => this.photosFiles = res);
    });
  }

  backToAuthor() {
    this.router.navigate(['/author'], {queryParams: this.author});
  }

  loadNewFile(data) {
    const file = data.target.files[0];
    const path = `${CollectionName.sections}/${this.author.sectionId}/${CollectionName.authors}/${this.author.id}/${CollectionName.photos}/${this.dataGetterService.GenerateUUID()}`;
    this.dataGetterService.UploadFile(path, file)
      .subscribe(value => {
        const newLibraryFile = new StorageFile();
        newLibraryFile.name = file.name;
        newLibraryFile.uri = `gs://${environment.firebaseConfig.storageBucket}/${path}`;
        // this.libraryFiles.push(newLibraryFile);
        this.dataGetterService.SetAuthorPhotosFile(this.author, newLibraryFile);
      });
  }

  showPhotoViewer(photoFile: StorageFile) {
    this.dataGetterService.GetImgURL(photoFile.uri).subscribe(url => {
      this.photoViewerEnabled = true;
      this.currentPhotoURL = url;
    });
  }

  hidePhotoViewer() {
    this.photoViewerEnabled = false;
  }
}
