import {StorageFile} from './storage-file';

export class Author {
  id: string;
  sectionId: string;
  imgURI: string;
  name: string;
  status: string;
  shortInfo: string;
  biography: string;
  library: StorageFile[];
  indicators: StorageFile[];
  photos: StorageFile[];

  constructor() {
    this.id = 'none';
    this.sectionId = 'none';
    this.imgURI = 'none';
    this.name = 'none';
    this.status = 'none';
    this.shortInfo = 'none';
    this.biography = 'none';
    this.library = [];
    this.indicators = [];
    this.photos = [];
  }

  public static clone(author: Author): Author {
    const newAuthor = new Author();
    newAuthor.id = author.id;
    newAuthor.sectionId = author.sectionId;
    newAuthor.imgURI = author.imgURI;
    newAuthor.name = author.name;
    newAuthor.status = author.status;
    newAuthor.shortInfo = author.shortInfo;
    newAuthor.biography = author.biography;
    newAuthor.library = author.library;
    newAuthor.indicators = author.indicators;
    newAuthor.photos = author.photos;
    return newAuthor;
  }
}
