import {Component, Input, OnInit} from '@angular/core';
import {Author} from '../models/author';
import {DataGetterService} from '../services/data-getter.service';

@Component({
  selector: 'app-section-author',
  templateUrl: './section-author.component.html',
  styleUrls: ['./section-author.component.scss']
})
export class SectionAuthorComponent implements OnInit {

  @Input() author: Author;
  authorImgURL: string;
  constructor(public readonly dataGetterService: DataGetterService) { }

  ngOnInit(): void {
    this.dataGetterService.GetImgURL(this.author.imgURI).subscribe(url => this.authorImgURL = url);
    // this.author.imgURLObservable.subscribe(url => this.authorImgURL = url );
  }

}
