import { Component, OnInit } from '@angular/core';
import {Author} from '../models/author';
import {ActivatedRoute, Router} from '@angular/router';
import {DataGetterService} from '../services/data-getter.service';
import {CollectionName} from '../models/collection-name.enum';
import {environment} from '../../environments/environment';
import {StorageFile} from '../models/storage-file';

@Component({
  selector: 'app-library',
  templateUrl: './library.component.html',
  styleUrls: ['./library.component.scss']
})
export class LibraryComponent implements OnInit {

  author: Author;
  libraryFiles: StorageFile[];

  constructor(public readonly activatedRoute: ActivatedRoute,
              public readonly dataGetterService: DataGetterService,
              public readonly router: Router) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((author: any) => {
      this.author = author;
      this.dataGetterService.GetAuthorLibraryFiles(this.author).subscribe(res => this.libraryFiles = res);
    });
  }

  backToAuthor() {
    this.router.navigate(['/author'], {queryParams: this.author});
  }

  loadNewFile(data) {
    const file = data.target.files[0];
    const path = `${CollectionName.sections}/${this.author.sectionId}/${CollectionName.authors}/${this.author.id}/${CollectionName.library}/${this.dataGetterService.GenerateUUID()}`;
    this.dataGetterService.UploadFile(path, file)
      .subscribe(value => {
        const newLibraryFile = new StorageFile();
        newLibraryFile.name = file.name;
        newLibraryFile.uri = `gs://${environment.firebaseConfig.storageBucket}/${path}`;
        // this.libraryFiles.push(newLibraryFile);
        this.dataGetterService.SetAuthorLibraryFile(this.author, newLibraryFile);
      });
  }
}
