import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Section} from '../models/section';
import {Author} from '../models/author';
import {DataGetterService} from '../services/data-getter.service';

@Component({
  selector: 'app-section',
  templateUrl: './section.component.html',
  styleUrls: ['./section.component.scss']
})
export class SectionComponent implements OnInit {

  section: Section;
  sectionBackgroundImgURL;
  authors: Author[];

  constructor(protected activatedRoute: ActivatedRoute,
              public readonly router: Router,
              public readonly dataGetterService: DataGetterService) {
    // activatedRoute.queryParams.subscribe((section: Section) => {
    //  this.section = section; });
    // activatedRoute.snapshot.paramMap.get('id');

  }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((section: Section) => {
      this.section = section;
    });

    this.refreshAuthors();

    this.dataGetterService.GetImgURL(this.section.titleBackgroundImgURI).subscribe(url => {
      this.sectionBackgroundImgURL = url;
    });
  }

  refreshAuthors() {
    this.dataGetterService.GetAuthors(this.section.id).subscribe(
      authors => {
        this.authors = authors;
        console.log(this.authors);
      });
  }
  addNewAuthor(): void {
    const newAuthor = new Author();
    newAuthor.sectionId = this.section.id;
    newAuthor.imgURI = this.dataGetterService.GetDummyAuthorImgURI();
    this.dataGetterService.AddAuthor(this.section.id, newAuthor)
      .subscribe(
        value => {
          this.dataGetterService.GetAuthor(this.section.id, value.id).subscribe(res => {
            this.router.navigate(['/edit-author'], { queryParams: res });
          });
          });
  }

}
