import { Component, OnInit } from '@angular/core';
import {Author} from '../models/author';
import {ActivatedRoute, Router} from '@angular/router';
import {DataGetterService} from '../services/data-getter.service';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';

@Component({
  selector: 'app-edit-biography',
  templateUrl: './edit-biography.component.html',
  styleUrls: ['./edit-biography.component.scss']
})
export class EditBiographyComponent implements OnInit {

  author: Author;
  editor = ClassicEditor;

  constructor(public readonly activatedRoute: ActivatedRoute,
              public readonly dataGetterService: DataGetterService,
              public readonly router: Router) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((author: any) => {
      this.author = Author.clone(author);
    });
  }

  saveAuthor() {
    this.dataGetterService.SetAuthor(this.author)
      .subscribe(
        value => {
          alert(`biography saved!`);
          this.router.navigate(['/biography'], {queryParams: this.author});
        },
        error => {
          alert(`Error ${error}`);
        });
  }

}
